import { PoolApiInfo } from '@/types'
import useWindowWidth from '@cetus/hooks/src/useWindowWidth'
import { formatPrice } from '@cetus/utils'
import { Box, HStack, Stack, Text } from '@chakra-ui/react'
import { useMemo } from 'react'
import AprTooltip from '../../common/aprTooltip'
import StatsInfo from './StatsInfo'

type PoolsStatsProps = {
  apiPoolInfo: PoolApiInfo
  apiPoolInfoLoading: boolean
  tvlLoading: boolean
  totalTvl: string
}

function PoolsStats({ apiPoolInfo, apiPoolInfoLoading, tvlLoading, totalTvl }: PoolsStatsProps) {
  const cursorStyle = useMemo(() => {
    if (apiPoolInfo?.haveFarming || (apiPoolInfo?.miningAprList && apiPoolInfo?.miningAprList.length > 0)) {
      return 'help'
    }
    return 'text'
  }, [apiPoolInfo])
  const { isApp } = useWindowWidth()
  return (
    <Stack flexDir={{ base: 'column', lg: 'row' }} gap={{ base: '20px', lg: '28px' }}>
      <StatsInfo
        label="Pool APR"
        value={
          <AprTooltip poolInfo={apiPoolInfo} placement={isApp ? 'bottom-end' : 'bottom'}>
            <HStack justify="flex-end" align="flex-end" gap="4px" lineHeight="14px" h="14px">
              <Text
                as="span"
                fontSize="14px"
                color={cursorStyle == 'help' ? 'primary' : 'text_caption'}
                fontWeight="500"
                cursor={cursorStyle}
                // textUnderlineOffset="2px"
                // textDecoration={cursorStyle == 'help' ? 'underline dotted' : 'none'}
                pos="relative"
              >
                {apiPoolInfo?.feeAndMiningAprDisplay !== '' ? apiPoolInfo?.feeAndMiningAprDisplay : '0%'}
                {cursorStyle == 'help' && <Box w="100%" borderBottom="1px dotted" borderColor="primary" pos="absolute" bottom="-1px" />}
              </Text>

              {apiPoolInfo?.haveFarming && (
                <Text fontSize="12px" lineHeight="12px" h="12px" color="primary" fontWeight="500">
                  +{apiPoolInfo?.farmingAprDisplay}
                </Text>
              )}
            </HStack>
          </AprTooltip>
        }
      />
      <StatsInfo
        label="TVL"
        value={totalTvl && totalTvl !== '--' ? `$${formatPrice(totalTvl, 2)}` : '--'}
        loading={apiPoolInfoLoading || tvlLoading}
      />
      <StatsInfo label="Volume (24H)" value={(apiPoolInfo?.volume24Display as string) || ''} loading={apiPoolInfoLoading} />
      <StatsInfo label="Fees (24H)" value={(apiPoolInfo?.fees24Display as string) || ''} loading={apiPoolInfoLoading} />
    </Stack>
  )
}

export default PoolsStats
