import usePoolInfo from '@/hooks/common/usePoolInfo'
import useLiquidityStore from '@/store/clmm'
import useTvlInfoStore from '@/store/clmm/liquidityTvl'
import { AddressCopyLink } from '@cetus/design'
import useExplorer from '@cetus/hooks/src/useExplorer'
import useQueryParams from '@cetus/hooks/src/useQueryParams'
import { SingleCoinImage } from '@cetus/ui-kit'
import { textEllipses } from '@cetus/utils'
import { Box, Grid, Heading, HStack, Progress, Skeleton, Text, VStack } from '@chakra-ui/react'
import InfoItem from '../../common/InfoItem'
import TokenCard from '../../common/TokenCard'

const getRatio = (value: string) => {
  if (value === '--') return '--'
  return `${value}%`
}

function PoolInfo() {
  const { apiPoolInfo, contractPoolInfo } = useLiquidityStore()
  const { getExplorerUrl } = useExplorer()
  const { poolAddress } = useQueryParams()
  const { tokenAAmount, tokenBAmount, tokenAAmountUSD, tokenBAmountUSD, totalAmountUSD, tvlLoading } = useTvlInfoStore()
  const { tokenARatio, tokenBRatio } = usePoolInfo(tokenAAmountUSD, totalAmountUSD)
  return (
    <VStack
      w={{ base: '100%', lg: '482px' }}
      gap={{ base: '20px', lg: '40px' }}
      p={{ base: '20px 8px 8px', lg: '20px 16px' }}
      borderRadius="16px"
      border="1px solid"
      borderColor="border"
      bg="bg_secondary"
      minW={{ base: '100%', lg: '482px' }}
      maxW={{ base: '100%', lg: '482px' }}
    >
      <VStack gap="20px" w="100%">
        <HStack w="100%" justify="space-between">
          <Heading fontSize="16px" fontWeight="400">
            Pool Info
          </Heading>
        </HStack>
        <Grid templateColumns={{ base: 'repeat(1, 1fr)', lg: 'repeat(2, 1fr)' }} w="100%" gap="12px">
          <InfoItem
            label="Address"
            value={
              <AddressCopyLink
                address={poolAddress || ''}
                fontSize="14px"
                color="text_caption"
                onClickLink={() => window.open(getExplorerUrl(poolAddress, 'poolAddress'))}
                wrapStyle={{ gap: '6px' }}
                iconGap="6px"
              />
            }
          />
          <InfoItem label="Pool ID" value={contractPoolInfo?.index !== undefined ? `#${contractPoolInfo?.index}` : '--'} />
          <InfoItem label="Tick Spacing" value={contractPoolInfo?.tickSpacing || ''} />

          <InfoItem label="Fee Tier" value={`${apiPoolInfo?.feeDisplay}` || '--'} />
        </Grid>
      </VStack>
      <VStack gap="20px" w="100%">
        <HStack justify="flex-start" w="100%">
          <Heading fontSize="16px" fontWeight="400">
            Pool Composition
          </Heading>
        </HStack>
        <VStack gap="16px" w="100%">
          <HStack w="100%" justify="space-between">
            <HStack gap="12px">
              <SingleCoinImage
                imageUrl={apiPoolInfo?.displayTokenA?.logo_url}
                w="28px"
                h="28px"
                coinType={apiPoolInfo?.displayTokenA ? apiPoolInfo?.displayTokenA?.coin_type : ''}
              />
              <VStack align="flex-start" gap="4px">
                <Text fontSize="16px" fontWeight="500" color="text_caption">
                  {textEllipses(apiPoolInfo?.displayTokenA?.symbol, 10)}
                </Text>
                {tvlLoading ? (
                  <Skeleton h="12px" w="46px" />
                ) : (
                  <Text fontSize="12px" fontWeight="500">
                    {apiPoolInfo?.isReverse ? getRatio(tokenBRatio) : getRatio(tokenARatio)}
                  </Text>
                )}
              </VStack>
            </HStack>
            <Box w="200px">
              <Progress
                value={Number(apiPoolInfo?.isReverse ? tokenBRatio : tokenARatio)}
                size="xs"
                h="4px"
                borderRadius="4px"
                bg={[tokenARatio, tokenBRatio].includes('--') ? 'border' : 'quote_green'}
                sx={{
                  'div[role="progressbar"]': {
                    bg: [tokenARatio, tokenBRatio].includes('--') ? 'background' : 'primary'
                  }
                }}
              />
            </Box>
            <HStack gap="12px">
              <VStack align="flex-end" gap="4px">
                <Text fontSize="16px" fontWeight="500" color="text_caption">
                  {textEllipses(apiPoolInfo?.displayTokenB?.symbol, 10)}
                </Text>
                {tvlLoading ? (
                  <Skeleton h="12px" w="46px" />
                ) : (
                  <Text fontSize="12px" fontWeight="500">
                    {apiPoolInfo?.isReverse ? getRatio(tokenARatio) : getRatio(tokenBRatio)}
                  </Text>
                )}
              </VStack>
              <SingleCoinImage
                imageUrl={apiPoolInfo?.displayTokenB?.logo_url}
                w="28px"
                h="28px"
                coinType={apiPoolInfo?.displayTokenB ? apiPoolInfo?.displayTokenB?.coin_type : ''}
              />
            </HStack>
          </HStack>
          <VStack w="100%" flexDir={apiPoolInfo?.isReverse ? 'column-reverse' : 'column'} gap="16px">
            <TokenCard loading={tvlLoading} token={apiPoolInfo?.tokenA} amount={tokenAAmount} amountUSD={tokenAAmountUSD} />
            <TokenCard loading={tvlLoading} token={apiPoolInfo?.tokenB} amount={tokenBAmount} amountUSD={tokenBAmountUSD} />
          </VStack>
        </VStack>
      </VStack>
    </VStack>
  )
}

export default PoolInfo
